.program {
  padding-top: 40px;
  padding-bottom: 145px;
  background-color: var(--primary-title-color);

  @include mq(tablet) {
    padding-top: 60px;
    padding-bottom: 180px;
  }

  @include mq(desktop) {
    padding-top: 80px;
    padding-bottom: 237px;
  }
}

.program-title {
  font-size: 20px;
  margin-bottom: 40px;
  color: var(--secondary-accent-title-color);

  @include mq(mobile-only) {
    text-align: center;
  }

  @include mq(tablet) {
    font-size: 26px;
    margin-left: 32px;
    margin-bottom: 40px;
  }

  @include mq(desktop) {
    font-size: 46px;
    margin-left: 16px;
    margin-bottom: 80px;
  }
}

.program__list-item {
  position: relative;

  @include mq(no-desktop) {
    &:not(:last-child) {
      padding-bottom: 40px;
      border-left: 1px solid #ffffff;
    }
  }
  @include mq(tablet-only) {
    padding-left: 103px;
  }

  @include mq(mobile-only) {
    padding-left: 26px;
    width: 240px;
  }

  @include mq(desktop) {
    padding-left: 0;
    width: 240px;
  }

  @include mq(desktop) {
    &:not(:last-child) {
      margin-right: 40px;
    }
  }
}

.program__list-item::before {
  position: absolute;
  left: -6px;
  top: -1px;
  content: '';
  width: 12px;
  height: 12px;
  border-radius: 50%;
  border: none;
  background: var(--accent-color);

  @include mq(desktop) {
    left: 0;
    top: -47px;
    width: 16px;
    height: 16px;
  }
}

.program-container {
  position: relative;
}

.program-container::after {
  position: absolute;
  content: '';
  width: 150px;
  height: 137px;
  right: 0;
  bottom: -106px;
  background-image: url(./../images/program/mobile-main-pic.png);
  background-size: 150px 137px;
}

@media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
  .program-container::after {
    background-image: url(./../images/program/mobile-main-pic@2x.png);
  }
}

@media screen and (min-width: 767px) {
  .program-container::after {
    width: 368px;
    height: 268px;
    top: 710px;
    background-image: url(./../images/program/tablet-main-pic.png);
    background-size: 368px 268px;
  }

  @media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
    .program-container::after {
      background-image: url(./../images/program/tablet-main-pic@2x.png);
    }
  }
}

@media screen and (min-width: 1679px) {
  .program-container::after {
    width: 684px;
    height: 510px;
    right: 996px;
    top: 577px;
    background-image: url(./../images/program/desctop-main-pic.png);
    background-size: 684px 510px;
  }

  @media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
    .program-container::after {
      background-image: url(./../images/program/desctop-main-pic@2x.png);
    }
  }
}

.subtitle {
  font-weight: 700;
  font-size: 14px;
  line-height: 2;

  letter-spacing: 0.08em;
  text-transform: uppercase;

  color: var(--secondary-accent-title-color);

  @include mq(no-desktop) {
    position: relative;
    top: -10px;
  }

  @include mq(desktop) {
    font-size: 20px;
    line-height: 1.4;
  }
}

.subtitle::first-letter {
  @include mq(desktop) {
    font-size: 30px;
    line-height: 0.9;
  }
}

.grammar {
  position: relative;
  margin-bottom: 4px;
  margin-left: 23px;
  font-weight: 700;
  font-size: 12px;
  line-height: 1.64;
  letter-spacing: 0.08em;

  color: var(--accent-color);

  &::before {
    position: absolute;
    left: -22px;
    top: 9px;
    content: '';
    width: 14px;
    height: 2px;
    background-color: var(--accent-color);
  }

  @include mq(desktop) {
    margin-bottom: 8px;
    margin-left: 27px;
    font-size: 20px;
    line-height: 1.64;
    margin-right: 0px;

    &::before {
      left: -27px;
      top: 16px;
      content: '';
      width: 20px;
      height: 2px;
    }
  }
}

.description {
  font-weight: 500;
  font-size: 12px;
  line-height: 1.67;
  letter-spacing: 0.08em;

  color: #b9b9b9;

  opacity: 0.5;

  @include mq(desktop) {
    font-size: 18px;
    line-height: 1.55;
  }
}

.program__grammar-list-item {
  @include mq(tablet-only) {
    width: 160px;
  }

  @include mq(mobile-only) {
    &:not(:first-child) {
      margin-top: 4px;
    }
  }

  @include mq(no-desktop) {
    &:not(:last-child) {
      margin-right: 32px;
    }
  }
}

.program__grammar-list-item:not(:first-child) {
  @include mq(desktop) {
    margin-top: 24px;
  }
}

.program__list-grammar {
  @include mq(tablet-only) {
    display: flex;
  }

  @include mq(desktop) {
    margin-top: 24px;
  }
}

@media screen and (max-width: 479px) {
  .program__list {
    margin-left: 47px;
  }
}

.program__list {
  @include mq(no-desktop) {
    margin-left: 100px;
  }

  @include mq(desktop) {
    display: flex;
    padding-top: 40px;
    justify-content: center;
    border-top: 1px solid #ffffff;
  }
}
